export function convertValue(item) {
    const newItem = {}
    // tabGroup
    if (item.type >= 0 && item.type <= 4) {
        if (item.flag_autoCast) {
            newItem.tabGroup = 'EXPL'
        } else {
            newItem.tabGroup = 'HAND'
        }
    } else {
        newItem['tabGroup'] = skillTypeDef[item.type]
    }
    Object.entries(item).forEach(([key, value]) => {
        if (key == "name") {
            newItem[key] = skillFieldDef[key] ? skillFieldDef[key](value) : value
        } else {
            newItem[key] = skillFieldDef[key] ? skillFieldDef[key](value) : value
        }
    })
    // 
    const grade = gradeModel.calcSkillGrade(item.id)
    newItem['grade'] = grade.totalGrade.toFixed(2)
    return newItem
}